<template>
  <div id="singleBlog">
    <h1> {{blog.title|toUpperCase}}</h1>
    <article class="singleBlog">{{blog.body}}</article>
  </div>
</template>

<script>
import axios  from 'axios'
export default {
  data(){
    return {
      id: this.$route.params.id,
      blog: {}
    }
  },

  created() {
    axios
      .get('https://jsonplaceholder.typicode.com/posts/' + this.id)
      .then((response) => {
        this.blog = response.data;
      })

  },

  computed: {
  },

  filters: {
  }
}
</script>

